import { Component } from "react";
import "./App.css";
import Topbar from "./Components/Topbar";
import Filter from "./Components/Filter";
import Card from "./Components/Card";
import Footer from "./Components/Footer";

class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      fliterData: [{ id: 0, title: "All" }],
      storiesData: [],
      currentFilterSelected: "All",
    };
  }

  componentDidMount() {
    fetch("https://api.edyoda.com/v1/blog/postcategories/")
      .then((res) => res.json())
      .then((result) =>
        this.setState({ fliterData: [...this.state.fliterData, ...result] })
      );
    fetch("https://api.edyoda.com/v1/blog/")
      .then((res) => res.json())
      .then((result) => this.setState({ storiesData: result }));
  }

  updateCurrentFilter = (e) => {
    this.setState({ currentFilterSelected: e.target.innerHTML });
  };

  render() {
    return (
      <div>
        {/*TOPBAR START*/}
        <Topbar />
        {/*TOPBAR END*/}
        {/*FILTER START*/}
        <div className="HomepageContainer">
          <div className="LatestPostContainer"></div>
          <div>
            <h3 className="latestPost">Latest Posts</h3>
            <div>
              <div className="FilterSection">
                <div className="FilterIconContainer">
                  <div className="FilterIcon">
                    <img
                      alt="Filter Icon"
                      src="data:image/png;base64,"
                    />
                  </div>
                  <p className="FilterText">Filter by Category</p>
                </div>
                <div className="CategoryList">
                  {this.state.fliterData.map(({ id, title }, index) => (
                    <Filter
                      updateFilter={this.updateCurrentFilter}
                      selected={this.state.currentFilterSelected}
                      id={id}
                      title={title}
                      key={index}
                    />
                  ))}
                </div>
              </div>
            </div>
          </div>
          {/*FILTER ENDS*/}
          {/*STORIES START*/}
          <div className="CardSection">
            {this.state.storiesData.map(
              ({ id, small_image, title, description, posted_on }, index) => (
                <Card
                  id={id}
                  imageUrl={small_image}
                  datePosted={posted_on}
                  title={title}
                  description={description}
                  key={index}
                />
              )
            )}
          </div>
          {/*STORIES ENDS*/}
        </div>
        {/*FOOTER STARTS*/}
        <Footer />
        {/*FOOTER ENDS*/}
      </div>
    );
  }
}

export default App;
